






















































































import Vue from 'vue'
import Component from 'vue-class-component'
import { Model, Prop } from 'vue-property-decorator'

@Component
export default class SignupForm extends Vue {
  @Model('change')
  formError!: string
  @Prop({ type: Boolean, default: false })
  loading!: boolean
  form = {
    username: '',
    password: '',
    passwordReword: '',
    mobile: '',
    license: false
  }
  rules = {
    username: [
      { max: 20, message: '长度不大于 20 个字符', trigger: 'blur' },
      { validator: this.validateUsername, trigger: 'blur' }
    ],
    password: [
      { required: true, message: '请输入密码', trigger: 'blur' },
      { max: 20, message: '长度不大于 20 个字符', trigger: 'blur' }
    ],
    passwordReword: [
      {
        required: true,
        validator: this.validatePasswordReword,
        trigger: 'blur'
      }
    ],
    mobile: [
      { required: true, validator: this.validateMobile, trigger: 'blur' }
    ]
  }

  setError(e?: string) {
    this.$emit('change', e)
  }
  submit(): void {
    const form = 'form'
    ;(this.$refs[form] as any).validate((valid: boolean) => {
      if (valid) {
        if (!this.form.license) {
          this.setError('请同意用户使用协议')
          return null
        }
        this.$emit('submit', this.form)
      }
    })
  }
  reset(): void {
    const form = 'form'
    ;(this.$refs[form] as any).resetFields()
  }
  validateUsername(rule: object, value: string, callback: (e?: Error) => void) {
    this.setError()
    if (value === '') {
      return callback(new Error('请输入用户名'))
    }
    // return callback(); // test
    this.$emit('validate', this.form.username, callback)
  }
  validatePasswordReword(
    rule: object,
    value: string,
    callback: (e?: Error) => void
  ): void {
    this.setError()
    if (!value) {
      callback(new Error('请再次输入密码'))
    } else if (this.form.password !== value) {
      callback(new Error('两次输入密码不一致'))
    } else {
      callback()
    }
  }
  validateMobile(
    rule: object,
    value: string,
    callback: (e?: Error) => void
  ): void {
    this.setError()
    const phoneReg = /^[1][3,4,5,7,8][0-9]{9}$/
    if (!value) {
      callback(new Error('请输入手机号'))
    } else if (!phoneReg.test(value)) {
      callback(new Error('请正确输入手机号'))
    } else {
      callback()
    }
  }
  validateError(rule: object, value: string, callback: (e?: Error) => void) {
    this.setError()
    if (this.formError) {
      callback(new Error(this.formError))
    } else {
      callback()
    }
  }
  redirectHandle() {
    this.$router.push({ name: 'Login' })
  }
}
