























import { Vue, Component } from 'vue-property-decorator'
import SignupForm from '@/components/user/SignupForm.vue'
import LoginFooter from '@/components/user/LoginFooter.vue'

@Component({
  components: {
    SignupForm,
    LoginFooter
  }
})
export default class Signup extends Vue {
  loading = false
  formError = ''
  submitHandle(form: any) {
    this.loading = true
    this.$http
      .post('/api/signup', form)
      .then(data => {
        this.loading = false
        this.$router.push({ name: 'Signup' })
      })
      .catch(error => {
        this.loading = false
        this.formError = '服务器开小差了，请稍后再试'
      })
  }
  validateHandle(username: string, callback: (e?: Error) => void) {
    this.$http
      .post('/api/signup/validate', username)
      .then(re => {
        if (re.data.type === 0) {
          callback()
        } else if (re.data.type === 1) {
          callback(new Error('用户名已被占用'))
        } else if (re.data.type === 2) {
          callback(new Error('用户名只能由英文、数字、下划线构成'))
        } else if (re.data.message) {
          callback(new Error(re.data.message))
        } else {
          callback()
        }
      })
      .catch(e => {
        // TODO: 错误处理
      })
  }
}
